import { Component, OnInit, OnDestroy } from '@angular/core';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-selectionner-jeu-video',
	templateUrl: './selectionner-jeu-video.page.html',
	styleUrls: [ './selectionner-jeu-video.page.scss' ]
})
export class SelectionnerJeuVideoPage implements OnInit, OnDestroy {
	searchInput: string;
	jeuxVideo: JeuVideo[];

	private JeuVideoSub: Subscription;

	constructor(private jeuxVideoService: DataJeuVideoService, private navCtrl: NavController) {
		this.jeuxVideo = [ ...jeuxVideoService.jeuVideoNonJoue ];
	}

	ngOnInit() {
		this.JeuVideoSub = this.jeuxVideoService.jeuVideoNonJoueChanged.subscribe((jeuvideo) => {
			this.jeuxVideo = jeuvideo;
		});
	}

	ngOnDestroy() {
		this.JeuVideoSub.unsubscribe();
	}

	goBack() {
		this.navCtrl.navigateBack([ '/medias/jeux-video' ]);
	}

	goToAjouter() {
		this.navCtrl.navigateForward([ '/selectionner-jeu-video/ajouter-jeu-video' ]);
	}

	showInfo(id: number) {
		this.navCtrl.navigateForward([ '/info-add-jeu-video/' + id ]);
	}
}
