import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SelectionnerJeuVideoPage } from './selectionner-jeu-video.page';

const routes: Routes = [
  {
    path: '',
    component: SelectionnerJeuVideoPage
  },
  {
    path: 'ajouter-jeu-video',
    loadChildren: () => import('./ajouter-jeu-video/ajouter-jeu-video.module').then( m => m.AjouterJeuVideoPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SelectionnerJeuVideoPageRoutingModule {}
